.page {
  height: 100%;
  font-size: 16px;
  line-height: 1.4;
  color: #1a1a1a;
  font-family: "DIN Pro", "Arial", sans-serif;
}
.page__body {
  min-height: 100%;
  background-color: #fff;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  overflow-x: hidden;
}
.page__body::-webkit-scrollbar {
  width: 8px;
}
.page__body::-webkit-scrollbar-track {
  background-color: #ed2e38;
}
.page__body::-webkit-scrollbar-thumb {
  background-color: #3c324c;
}
.page__body--overflow-hidden {
  overflow: hidden;
  padding-right: 8px;
}
.page__body--admin {
  background-color: #f0f0ea;
}
.page__content {
  margin-left: 280px;
  width: 1280px;
  padding-bottom: 80px;
}
.page__content a,
.page__content button {
  cursor: pointer;
}
.page__breadcrumbs {
  display: flex;
  margin: 16px 0;
  padding: 0;
  list-style: none;
}
.page__breadcrumb {
  display: flex;
  align-items: center;
}
.page__breadcrumb svg {
  margin-left: 8px;
  color: #ed2e38;
}
.page__breadcrumb:not(:first-child) {
  margin-left: 8px;
}
.page__breadcrumb:last-child {
  color: #ed2e38;
}
.page__link-wrapper {
  display: flex;
  align-items: flex-end;
}
.page__title {
  font-size: 16px;
  display: flex;
  margin: 0;
  padding: 8px 16px;
  background-color: #fff;
  font-weight: 600;
  border-radius: 8px 8px 0 0;
}
.page__link {
  padding: 8px 16px;
  font-size: 16px;
  cursor: pointer;
}
.page__link:hover {
  color: #3c324c;
}
.page__table {
  border-spacing: 2px;
}
.page__table th,
.page__table td {
  background-color: #fff;
  padding: 10px 16px;
}
.page__table th div,
.page__table td div {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.section-template {
